import React from "react";
import styled from 'styled-components';

function Buttonglass(props) {


const Btn =styled.div`
position: relative;
width: 155px;
height: 50px;
margin: 0px;

&:before{
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -5px;
  width: 50px;
  height: 10px;
  background: #f00;
  border-radius: 10px;
  transition: 0.5s;
  transition-delay: 0s;
}

&:after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -5px;
  width: 50px;
  height: 10px;
  background: #f00;
  border-radius: 10px;
  transition: 0.5s;
  transition-delay: 0s;
}

&:after {
  bottom: initial;
  top: -5px;
}
&:hover:after {
  bottom: initial;
  top: 0;
}

&:hover:before,
&:hover:after {
  bottom: 0;
  height: 50%;
  width: 80%;
  border-radius: 30px;
  transition-delay: 0.5s;
}

&:hover a:before,
&:focus a:before {
  transform: skewX(45deg) translateX(220%);
}


&:before,
&:after {
  background: ${props.background};
  box-shadow: 0 0 5px ${props.background}, 0 0 15px ${props.background}, 0 0 30px ${props.background},
    0 0 60px ${props.background};
}

`;


const BtnA =styled.a`
color: inherit;
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
display: flex;
justify-content: center;
align-items: center;
background: rgba(255, 255, 255, 0.05);
box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
border-top: 1px solid rgba(255, 255, 255, 0.1);
border-bottom: 1px solid rgba(255, 255, 255, 0.1);
border-radius: 30px;
z-index: 1;
letter-spacing: 1px;
text-decoration: none;
overflow: hidden;
transition: 0.5s;
backdrop-filter: blur(15px);
&:hover ,
&:focus  {
  letter-spacing: 4px;
}


&:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.15), transparent);
  transform: skewX(45deg) translateX(0);
  transition: 0.5s;
}
`;



  return (
    <>
<Btn><BtnA href={`${props.link}`}>{props.text}</BtnA></Btn>



    </>
  );
}

export default Buttonglass;




